@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: 'Source Sans Pro', sans-serif;
}

.App {
  font-size: 16px;
  margin: 0 auto;
  max-width: 60rem;
  display: grid;
  grid-template-columns: 1;
  grid-template-areas: "main";
}
.app-determine, .app-probability {
  padding: 0rem 2rem;
  border: 1px solid #ddd;
  border-radius: 0.5rem;
  margin-top: 1rem;
}
label {
  font-size: 1.5rem;
}


label {
  display: block;
}
input {
border-radius: 5px;
font-size: 1rem;
line-height: 1.5rem;
}
input[type=number] {
  display: inline-block;
  margin: 0.5rem 0 1rem 0;
  width: 10rem;
  border: 1px solid #000;
}
input[type=submit] {
  background-color: #518812;
  color: white;
  width: 9rem;
  padding: 0.25rem 1rem;
  margin-bottom: 1rem;
}
#outcomeProb {
  width: 3.5rem;
}
.hint {
  font-size: 1rem;
  color: #111;
  padding-left: 0.25rem;
}

p {
  font-size: 18px;
  line-height: 1.2;
}
.strong {
  font-weight: bold;
}
.output p {
  margin-top: 0;
}

.determine-output input[type=number] {
  width: 4rem;
}
.determine-output input[type=submit] {
  width: 12rem;
}
form:last-child {
  margin-top: 1rem;
}

.fraction-output {
  margin: 1rem 0;
  display: grid;
  grid-column-gap: 0.5rem;
  grid-row-gap: 0.5rem;
  justify-content: start;
  grid-auto-flow: row;
  grid-template-columns: repeat(5, 1fr);
}

.fraction-entry {
  display: inline;
  font-size: 1.5rem;
  padding: 0.25rem;
  border: 1px solid #ddd;
  border-radius: 0.25rem;
  text-align: center;
}

.fraction-line {
  display: block;
  border: 1px solid #ddd;
  margin: 0.25rem 0;
  width: 100%;
}

.App-intro {
  line-height: 1.2;
}

